<template>
    <div class="tables-basic">
        <b-breadcrumb>
            <b-breadcrumb-item></b-breadcrumb-item>
            <b-breadcrumb-item active></b-breadcrumb-item>
        </b-breadcrumb>
        <!--<h2 class="page-title"><span class="fw-semi-bold"></span></h2>-->
        <div id="screen">
            <div class="input-group mb-3">
                <div class="input-group-prepend">
                    <span class="input-group-text" id="inputGroup-sizing-default"></span>
                </div>
                <input type="text" class="form-control" v-model="searchNames" v-on:input ="search" aria-label="Default" aria-describedby="inputGroup-sizing-default">
            </div>
            <div class="input-group mb-3">
                <div class="input-group-prepend">
                    <label class="input-group-text" for="VIPSelect"></label>
                </div>
                <select class="custom-select" id="VIPSelect">
                    <option value="all" selected></option>
                    <option value="VIP1" >VIP1</option>
                    <option value="VIP2" >VIP2</option>
                    <option value="VIP3" >VIP3</option>
                </select>
            </div>
            <button type="button" v-on:click="search" class="btn btn-sm btn-outline-secondary "></button>
        </div>


        <b-row>
            <b-col>
                <Widget title="<h5><span class='fw-semi-bold'></span></h5>"
                        customHeader  close
                >
                    <div class="table-resposive">
                        <table class="table table-striped">
                            <thead>
                            <tr>
                                <th class="hidden-sm-down">
                                #
                                </th>
                                <th></th>
                                <th></th>
                                <th class="hidden-sm-down"></th>
                                <th class="hidden-sm-down"></th>
                                <th class="hidden-sm-down"></th>
                                <th class="hidden-sm-down"></th>
                                <th class="hidden-sm-down"></th>
                            </tr>
                            </thead>
                            <tbody>
                            <tr v-for="row in handledArray" :key="row.id">
                                <td>{{row.id}}</td>
                                <td>
                                    {{row.user}}
                                </td>
                                <td>
                                    {{row.wxID}}
                                </td>
                                <td>
                                    {{row.senior}}
                                </td>
                                <td class="text-semi-muted">
                                    {{row.integral}}
                                </td>
                                <td class="text-semi-muted">
                                    {{row.tellPhone}}
                                </td>
                                <td class="text-semi-muted">
                                    {{row.email}}
                                </td>
                                <td class="text-semi-muted">
                                    {{row.contact}}
                                </td>
                            </tr>
                            </tbody>
                        </table>
                    </div>
                    <div class="clearfix">
                        <div class="float-right">
                            <b-button variant="default" class="mr-xs" size="sm">/</b-button>
                        </div>
                        <!--<p>Basic table with styled content</p>-->

                    </div>
                </Widget>
            </b-col>
        </b-row>
    </div>
</template>

<script>
    import 'bootstrap/dist/css/bootstrap.min.css'
    import 'bootstrap/dist/js/bootstrap.min.js'
    import axios from 'axios'
    import $ from 'jquery';
    import Vue from 'vue';
    import Widget from '@/components/Widget/Widget';
    import 'imports-loader?jQuery=jquery,this=>window!jquery-sparkline'; // eslint-disable-line

    export default {
        name: 'Users',
        components: { Widget },
        data() {
            return {
                searchNames: '',
                handledArray: [],
                originalArray: [
                    {
                        id: 1,
                        user: 'admin',
                        wxID: 'weixinid',
                        senior: 'VIP1',
                        integral: 300,
                        tellPhone: '13012345678',
                        email: '13012345678',
                        contact: '750'
                    },
                    {
                        id: 2,
                        user: 'admin',
                        wxID: 'weixinid',
                        senior: 'VIP2',
                        integral: 300,
                        tellPhone: '13012345678',
                        email: '13012345678',
                        contact: '750'
                    },
                    {
                        id: 3,
                        user: 'admin',
                        wxID: 'weixinid',
                        senior: 'VIP3',
                        integral: 300,
                        tellPhone: '13012345678',
                        email: '13012345678',
                        contact: '750'
                    },


                ],
            };
        },
        methods: {
            // selectVIP: function(vip) {
            //     console.log(vip);
            //     // alert(vip);
            // },
            searchName() {
                let handledArray = [];
                let originalArray = this.originalArray;
                for (let co in originalArray){
                    let user = originalArray[co];
                    let name = user.user;
                    if(name.includes(this.searchNames)){
                        handledArray.push(user);
                    }
                }
                this.handledArray = handledArray;
            },
            search() {
                let userName = this.searchNames;
                // console.log(userName);
                let vip = $("#VIPSelect").val();
                // console.log(vip);
                let handledArray = [];
                let originalArray = this.originalArray;
                let temp = []
                for (let co in originalArray){
                    let user = originalArray[co];
                    let senior = user.senior;
                    if ('all' == vip || senior.includes(vip)){
                        temp.push(user);
                    }

                }
                for (let co in temp){
                    let user = originalArray[co];
                    let name = user.user;
                    if(name.includes(userName)){
                        handledArray.push(user);
                    }
                }
                this.handledArray = handledArray;
            },
            parseDate(date) {
                const dateSet = date.toDateString().split(' ');
                return `${date.toLocaleString('en-us', { month: 'long' })} ${dateSet[2]}, ${dateSet[3]}`;
            },
            checkAll(ev, checkbox) {
                const checkboxArr = (new Array(this[checkbox].length)).fill(ev.target.checked);
                Vue.set(this, checkbox, checkboxArr);
            },
            changeCheck(ev, checkbox, id) {
                this[checkbox][id] = ev.target.checked;
                if (!ev.target.checked) {
                    this[checkbox][0] = false;
                }
            },
            getRandomData() {
                const result = [];

                for (let i = 0; i <= 8; i += 1) {
                    result.push(Math.floor(Math.random() * 20) + 1);
                }

                return result;
            },
            initCharts() {
                const colors = ['#547fff', '#9964e3', '#f55d5d', '#ffc247', '#3abf94'];
                $.each($('.sparkline-chart'), (id, chart) => {
                    $(chart).sparkline(this.getRandomData(), {
                        type: 'bar',
                        barColor: colors[Math.floor(Math.random() * colors.length)],
                    });
                });
            },
        },
        mounted() {
            var _this= this;
            this.initCharts();
            axios.post('http://localhost:8087/queryUsers', {
                params:{
                    // user:"admin"
                }},
                {headers: {
                    'Access-Control-Allow-Origin' : 'http://localhost:8087'
                }}

            ).then(function(res){
                // console.log(res)
                _this.originalArray = res.data;
                _this.handledArray = res.data;
            })

        },

    };
</script>

